import { ApplicationInsights } from '@microsoft/applicationinsights-web';
import { constants } from '../constants';
import { AggregateError } from '../interfaces';
import { environment } from '../pagecomponents/environment';
import { IFormatError } from './formatService';

class AppInsightsServiceClass
{
  private appInsights: ApplicationInsights;
  private readonly customEventsVersion = 1;
  
  constructor()
  {
    console.log(process.env.NODE_ENV);
    if(typeof window !== "undefined" && environment.isDev == false)
    {
      this.appInsights = new ApplicationInsights({
        config: {
          instrumentationKey: constants.appInsightsInstrumentationKey,
        }
      });
      this.appInsights.loadAppInsights();
      this.appInsights.trackPageView();
    }
  }

  trackError(message: string, error: Error)
  {
    if(environment.isDev == true)
      return;
    this.appInsights.trackException({
      error,
      properties: {
        message,
        version: this.customEventsVersion
      }
    });
  }
  
  trackFormatError(error: IFormatError | AggregateError)
  {
    if(environment.isDev == true)
      return;
    let kinds = error instanceof AggregateError ? error.errors.map(e => e.kind) : [error.kind];
    this.appInsights.trackEvent({
      name: "FormatterError",
      properties: {
        errorKinds: kinds,
        version: this.customEventsVersion
      }
    });
  }
  
  trackFormat()
  {
    if(environment.isDev == true)
      return;
    this.appInsights.trackEvent({
      name: "FormatterCall",
      properties: {
        version: this.customEventsVersion
      }
    });
  }

  setUserId(userId: string) {
    if(environment.isDev == true)
      return;
    this.appInsights.setAuthenticatedUserContext(userId);
  }

  clearUserId() {
    if(environment.isDev == true)
      return;
    this.appInsights.clearAuthenticatedUserContext();
  }
}


export const AppInsightsService = new AppInsightsServiceClass();