import React from "react";
import { Layout } from "../components/Layout";
import { IHeaderProps } from '../components/Header';
import { Formatter } from "../components/Formatter";
import { Link } from "gatsby";
import { Issues } from "../components/Issues";

export class HomePage extends React.Component
{
  render()
  {
    let header: IHeaderProps = {
      url: "/",
      type: "website",
      title: "Power Query Formatter | Beautify your Power Query code",
      description: "Format your PowerQuery (M-Language) code with Power Query Formatter"
    };
    return (
      <Layout header={header}>
        <div>
          <h1>Beautify your Power Query (M-Language) Code with Power Query Formatter</h1>
          <div style={{marginBottom: "10px"}}>
            <p>
              Power Query Formatter is in Beta stadium, so don't expect it to work perfectly. If you encounter any issues please leave us a bug report so we can improve our service.
            </p>
            <p>At the moment only queries, <b>no section documents</b>, are supported. Have a look at the <a href="#issues">open issues</a> for more information.</p>
          </div>
        </div>
        <div>
          <Formatter/>
        </div>
        <div style={{marginTop: "20px"}}>
          <h1>How the formatter works</h1>
          <p>The formatter formats by trying to put as many statements as possible in single lines. Once the lines exceed the configured maximum line length the formatter forces the last most expression to break line. If the resulting layout still exceeds the line length the next expression will be forced to break line. The break line behavior is defined seperately for each expression. Some expressions - like e.g. Literals - cannot break line because it doesn't make sense. Other expressions always break line like the <b>let</b>-Expression. You can explore the options tab in the formatter to view the parameter set that the formatter supports. 
          </p>
          <p>
            There is also an <Link to="/api">Api</Link> that you can use to automatically format queries.
          </p>
          <p>The formatter uses your browser database to save your configured options and your last query.</p>
        </div>
        <div id="issues" style={{marginTop: "20px"}}>
          <Issues></Issues>
        </div>
      </Layout>
    );
  }
}

export default HomePage;