import React, { ChangeEvent } from "react";
import "./Formatter.sass";
import { ErrorDisplay } from "./ErrorDisplay";
import { FormatterOptions } from "./FormatterOptions";
import { IConfig } from 'm-formatter';
import { AppInsightsService } from '../misc/appInsightsService';
import { AggregateError } from '../interfaces';
import { StateService, IStateComponent } from '../misc/stateService';
import { FaEdit, FaCopy, FaPlay, FaCode, FaCog, FaFile } from "react-icons/fa";
import { Tooltip } from "./Misc";
import { range } from '../tool';
import { Optional } from "../interfaces"
import { FormatService, IFormatError } from '../misc/formatService';
import { lightThemeCss, darkThemeCss } from "../options";

interface IState
{
  code: string;
  errors: IFormatError[];
  errorMessage: string;
  toggleOptions: boolean;
  formatting: boolean;
  formatErr: string;
  theme: "light"|"dark";
  initialized: boolean;
  inputLines: number;
  editorState: "edit"|"display";
  fontSize: number;
}

export class Formatter extends React.Component<{}, IState> implements IStateComponent
{
  codeRef = React.createRef() as React.RefObject<HTMLDivElement>;
  optionComponent = React.createRef() as React.RefObject<FormatterOptions>;
  controlContainerRef = React.createRef() as React.RefObject<HTMLDivElement>;
  optionContainer = React.createRef() as React.RefObject<HTMLDivElement>;
  copyButtonRef = React.createRef() as React.RefObject<HTMLButtonElement>;
  copyState: "html"|"text"|null = null;
  formatterInput = React.createRef() as React.RefObject<HTMLDivElement>;

  lastConfig: IConfig;
  
  formatResult: string;
  
  get code(): string
  {
    return this.codeRef.current.innerText;
  }
  
  constructor(props)
  {
    super(props);
    this.state = {
      code: null,
      errors: null,
      errorMessage: null,
      toggleOptions: false,
      formatting: false,
      formatErr: null,
      theme: "light",
      initialized: false,
      inputLines: 10,
      editorState: "edit",
      fontSize: 1
    }
    StateService.registerComponent("formatter", this);
  }
  
  getState()
  {
    return {
      code: this.state.code,
      theme: this.state.theme
    }
  }
  
  async componentDidMount()
  {
    this.codeRef.current.addEventListener('keydown', (e) => {
      if(e.ctrlKey == true && e.keyCode == 13) //enter
      {
        this.format().then(e => {
        });
      }
      if(e.keyCode == 8 && this.state.editorState == "display")
      {
        this.back();
      }
    });
    
    let state = await StateService.retrieveState<IState>("formatter");
    this.codeRef.current.innerText = state?.code ?? "";
    this.setState({
      ...state,
      initialized: true
    });
    
    document.body.addEventListener("paste", this.onBodyPaste);
    document.addEventListener("copy", this.onCopy);
    window.addEventListener("keydown", this.onWindowKeydown);
    
    this.resizeInputBg();
  }
  
  componentWillUnmount()
  {
    StateService.storeState("formatter", this.getState())
    document.body.removeEventListener("paste", this.onBodyPaste);
    document.removeEventListener("copy", this.onCopy);
    window.removeEventListener("keydown", this.onWindowKeydown);
  }
  
  onEditorKeydown = (e: KeyboardEvent) => {
    if(e.keyCode === 9) //tab
    {
      event.preventDefault();
      var range = window.getSelection().getRangeAt(0);
      var tabNode = document.createTextNode("\u00a0\u00a0\u00a0\u00a0");
      range.insertNode(tabNode);
      range.setStartAfter(tabNode);
      range.setEndAfter(tabNode); 
    } 
  }
  
  onWindowKeydown = (e: KeyboardEvent) => {
    if(e.ctrlKey == true && e.keyCode == 13) //enter
    {
      this.format().then(e => {
      });
    }
    else if(e.keyCode == 8 && e.ctrlKey == true && this.state.editorState == "display")
    {
      this.back(true);
    }
    else if(e.keyCode == 8 && this.state.editorState == "display")
    {
      this.back();
    }
  }
  
  onBodyPaste = (event: ClipboardEvent) => {
    let paste = (event.clipboardData || (window as any).clipboardData).getData('text') as string;
    this.codeRef.current.innerText = paste;
    this.setState({
      code: paste
    });
    this.resizeInputBg();
    if(event.target == this.codeRef.current)
      event.preventDefault();
    if(this.optionComponent.current.state.formatOnPaste == true)
    {
      setTimeout(async () => {
        try 
        {
          await this.format(true);   
        } 
        catch (error) 
        {
          this.back(false);  
        }
      });
    }
  }
    
  makeTabIndentation(code: string)
  {
    return code
      .split(/\n/g)
      .map(l => l.replace(new RegExp(`^([ \u00A0]+)`, "g"), (s,g) => {
        let len = g.length;
        let rest = len % this.lastConfig.indentationLength;
        return "\t".repeat(Math.floor(len / this.lastConfig.indentationLength)) + " ".repeat(rest);
      }))
      .join("\n");
  }
  
  onCopy = (ev: ClipboardEvent) => {
    if(this.copyState == "html")
    {
      ev.clipboardData.setData("text/html", this.formatResult);
      ev.clipboardData.setData("text/plain", this.formatResult);
      ev.preventDefault();
    }
    else if(this.copyState == "text")
    {
      let code = this.code;
      if(this.optionComponent.current.state.indentKind == "Tabs")
        code = this.makeTabIndentation(code);
      ev.clipboardData.setData("text/plain", code)
      ev.preventDefault();
    }
    this.copyState = null;
  }
  
  onCopyClicked = () => {
    this.copyState = "text";
    document.execCommand("copy");
  }
  
  
  onCopyHtmlClicked = () => {
    this.copyState = "html";
    document.execCommand("copy");
  }
  
  onFormatClicked = async (ev) => 
  {
    await this.format();
  }
  
  onOptionsChanged = () => {
    if(this.state.editorState == "display")
    {
      let scroll = document.documentElement.scrollTop;
      let offset = scroll - this.optionContainer.current.offsetTop;
      this.format().then(() => {
        let top = this.optionContainer.current.offsetTop;
        document.documentElement.scrollTop = top + offset;
      });
    }
    this.setState({
      fontSize: this.optionComponent.current.state.fontSize
    })
  }
  
  onOptionsClicked = (top: boolean = false) => {
    if(top == true)
    {
      if(this.state.toggleOptions == false)
        this.toggleOptions();
      setTimeout(() => {
        if(this.state.toggleOptions == true)
          this.optionContainer.current.scrollIntoView({behavior: "smooth"});
      });
    }
    else
    {
      this.toggleOptions();
    }
  }
  
  resizeInputBg = () => {
    // this.formatterInput.current.style.height = Math.max(window.innerHeight, this.codeRef.current.clientHeight) + "px";
    let height = document.querySelector(".num-1").getBoundingClientRect().height;
    let lines = Math.ceil(this.codeRef.current.clientHeight / height) ;
    if(lines != this.state.inputLines)
    {
      this.setState({
        inputLines: lines
      });
    }
    
  }
  
  format = async (rethrow = false) => {
    if(this.state.code == null || this.state.code == "")
    {
      this.setState({
        formatErr: "Please enter some code to format"
      });
      return;
    }
    this.setState({
      formatting: true,
      formatErr: null,
      errors: null,
      errorMessage: null
    });
    
    try
    {
      let config = this.optionComponent.current.getConfig();
      if(this.optionComponent.current.state.customCss == false && config.inlineCssToggle == true)
        config.inlineCss = this.state.theme == "light" ? lightThemeCss : darkThemeCss;
        
      let result = await FormatService.format(this.state.code, this.optionComponent.current.getConfig());
      this.lastConfig = config;
      
      AppInsightsService.trackFormat();
        
      this.codeRef.current.innerHTML = result;
      this.formatResult = result;
      
      this.setState({
        code: this.code,
        editorState: "display"
      });
      this.resizeInputBg();
      
      if(this.optionComponent.current.state.copyOnFormat == true)
      {
        setTimeout(() => {
          this.onCopyClicked();
        });
      }
    }
    catch(error)
    {
      console.log(error);
      if(error instanceof AggregateError)
      {
        this.setState({errors: error.errors})
        AppInsightsService.trackFormatError(error);
      }
      else if((error as IFormatError).kind == "INVALID_CONFIG")
      {
        this.setState({
          errorMessage: "Invalid options passed. Please check if you have any invalid values configured in the options tab",
          toggleOptions: true
        });
        return;
      }
      else if((error as IFormatError).kind != null)
      {
        this.setState({errors: [error]})
        AppInsightsService.trackFormatError(error);
      }
      else
      {
        AppInsightsService.trackError("Unknown error during formatting", error);
      }
      if(rethrow == true)
        throw error;
    }
    finally
    {
      this.setState({
        formatting: false
      });
    }
  }

  toggleOptions = () => {
    this.setState({
      toggleOptions: !this.state.toggleOptions
    });
  }

  handleChange = (ev: ChangeEvent<any>) => {
    let d = {};
    let id = ev.target.name ?? ev.target.id;
    if(ev.target.type == "checkbox")
    {
      d[id] = ev.target.checked;
    }
    else if(id == "code")
    {
      d[id] = ev.target.innerText;
      this.resizeInputBg();
    }
    else
    {
      d[id] = ev.target.value;
    }
    this.setState(d);
    
    if(ev.target.name == "theme" && !String.isNullOrEmpty(this.state.code) && this.state.editorState == "display")
    {
      setTimeout(async () => {
        await this.format();
      });
    }
  }

  back = (reset: boolean = false) => {
    let state: Optional<IState> = {
      editorState: "edit"
    };
    this.formatResult = null;
    this.codeRef.current.innerText = this.state.code;
    if(reset == true)
    {
      state.code = null;
      this.codeRef.current.innerText = null;
    }
    
    this.setState(state as any);
    this.codeRef.current.focus();
    this.resizeInputBg();
    
  }
  
  render()
  {
    return (
      <div className="formatter-container">
        <div className="error-container">
          {
            this.state.errors != null ? (
              <div>
                <div className="error">
                  Errors occured
                </div>
                {
                  this.state.errors.map((e,i) => <div key={i}><ErrorDisplay error={e}></ErrorDisplay></div>) 
                }
                </div>
            ): ""
          }
        </div>
        <div>
          <span style={{marginRight: '10px'}}><b>Theme</b></span>
          <span className="control">
            <label className="radio">
              <input type="radio" name="theme" value="light" checked={this.state.theme == "light"} onChange={this.handleChange}/>
              Light
            </label>
            <label className="radio">
              <input type="radio" name="theme" value="dark" checked={this.state.theme == "dark"} onChange={this.handleChange}/>
              Dark
            </label>
          </span>
        </div>
        <div ref={this.controlContainerRef}>
          {
            this.state.errorMessage ? <div className="error">{this.state.errorMessage}</div> : ""
          }
          <div className="formatter-input-controls">
            <div className="space-between">
              <div style={{display: "flex"}}>
                <Tooltip text={<><b>format</b> (ctrl+enter)</>}>
                  <button onClick={this.onFormatClicked} className={`button action-button ${this.state.formatting == true ? "is-loading" : ""}`} disabled={this.state.formatting == true}><FaPlay></FaPlay></button>
                </Tooltip>
                {
                  this.state.editorState == "display" ? (
                    <>
                      <Tooltip text={<><b>edit code</b> (backspace)</>}>
                        <button className="button action-button" onClick={() => this.back(false)}><FaEdit></FaEdit></button>
                      </Tooltip>
                    </>
                  ) : ""
                }
                {
                  this.state.code != null ? (
                    <Tooltip text={<><b>new query</b> (ctrl+n)</>}>
                      <button className="button action-button" onClick={() => this.back(true)}><FaFile></FaFile></button>
                    </Tooltip>
                  ): ""
                }
              </div>
                <div style={{display: "flex"}}>
                {
                  this.state.editorState == "display" ? (
                    <>
                      <Tooltip text="copy code">
                        <button ref={this.copyButtonRef} className="button action-button" onClick={this.onCopyClicked}><FaCopy></FaCopy></button>
                      </Tooltip>
                      <Tooltip text="copy HTML">
                        <button className="button action-button" title="Copy HTML to your clipboard. You can paste this into word or other rich text editors to have colored code." onClick={this.onCopyHtmlClicked}><FaCode></FaCode></button>
                      </Tooltip>
                    </>
                  ): ""
                }
              </div>
              <div>
                <Tooltip text="options">
                  <button onClick={() => this.onOptionsClicked(true)} className={`button action-button ${this.state.formatting == true ? "is-loading" : ""}`}><FaCog></FaCog></button>
                </Tooltip>
              </div>
            </div>
          </div>
        </div>
        <div>
          <div ref={this.formatterInput} className={`formatter-input theme-${this.state.theme}`} style={{fontSize: this.state.fontSize + "em"}}>
            <div className="nums" style={{width: (this.state.inputLines.toString().length * 0.9) + "em"}}>
              {
                range(0, this.state.inputLines).map((l,i) => (
                  <div className={`${i%2 == 0 ? "num-1" : "num-2"}`} key={i}>{i+1}</div>
                ))
              }
            </div>
            <div className="code-container">
              <div className="bg">
                {
                  range(0, this.state.inputLines).map((l,i) => (
                    <div className={`code-line ${i%2 == 0 ? "code-line-1" : "code-line-2"}`} key={i}>&nbsp;</div>
                  ))
                }
              </div>
              {
                String.isNullOrEmpty(this.state.code) ? (
                  <div className="placeholder">Place your Power Query Code here</div>
                ): ""
              }
              {/* EDITOR */}
              <div spellCheck={false} contentEditable={this.state.editorState == "edit"} onKeyDown={(e) => this.onEditorKeydown(e as any)} onInput={this.handleChange} ref={this.codeRef} className="textarea code" id="code"></div>
            </div>
          </div>
        </div>
        <div className="formatter-errors">
        {
          this.state.formatErr != null ? <span className="error">{this.state.formatErr}</span> : ""
        }
        </div>
        <div>
          {
            this.state.errorMessage ? <div className="error">{this.state.errorMessage}</div> : ""
          }
          <div className="formatter-input-controls">
            <div className="space-between">
              <div style={{display: "flex"}}>
                <Tooltip text={<><b>format</b> (ctrl+enter)</>}>
                  <button onClick={this.onFormatClicked} className={`button action-button ${this.state.formatting == true ? "is-loading" : ""}`} disabled={this.state.formatting == true}><FaPlay></FaPlay></button>
                </Tooltip>
                {
                  this.state.editorState == "display" ? (
                    <>
                      <Tooltip text={<><b>edit code</b> (backspace)</>}>
                        <button className="button action-button" onClick={() => this.back(false)}><FaEdit></FaEdit></button>
                      </Tooltip>
                    </>
                  ) : ""
                }
                {
                  this.state.code != null ? (
                    <Tooltip text={<><b>new query</b> (ctrl+n)</>}>
                      <button className="button action-button" onClick={() => this.back(true)}><FaFile></FaFile></button>
                    </Tooltip>
                  ): ""
                }
              </div>
                <div style={{display: "flex"}}>
                {
                  this.state.editorState == "display" ? (
                    <>
                      <Tooltip text="copy code">
                        <button ref={this.copyButtonRef} className="button action-button" onClick={this.onCopyClicked}><FaCopy></FaCopy></button>
                      </Tooltip>
                      <Tooltip text="copy HTML">
                        <button className="button action-button" title="Copy HTML to your clipboard. You can paste this into word or other rich text editors to have colored code." onClick={this.onCopyHtmlClicked}><FaCode></FaCode></button>
                      </Tooltip>
                    </>
                  ): ""
                }
              </div>
              <div>
                <Tooltip text="options">
                  <button onClick={() => this.onOptionsClicked(false)} className={`button ${this.state.toggleOptions == true ? "active": ""} action-button ${this.state.formatting == true ? "is-loading" : ""}`}><FaCog></FaCog></button>
                </Tooltip>
              </div>
            </div>
          </div>
          <div ref={this.optionContainer} className={`${this.state.toggleOptions == false ? "no-display" : ""}`}>
            <FormatterOptions ref={this.optionComponent} onChange={this.onOptionsChanged}></FormatterOptions>
          </div>
        </div>
      </div>
    )
  }
}