import { DbService } from './dbService';

export interface IStateComponent
{
  getState(): any;
}

class StateServiceClass
{
  dict: {id: string; store: boolean; value: any}[] = [];
  components: {id: string, comp: IStateComponent}[] = [];
  constructor()
  {
    if(typeof window !== "undefined")
      window.addEventListener("beforeunload", this.onBeforeUnload)
  }
  
  onBeforeUnload = async (ev) =>
  {
    for(let c of this.components)
    {
      this.storeState(c.id, c.comp.getState());
    }
    for(let el of this.dict.filter(e => e.store == true))
    {
      await DbService.store(el.id, el.value);
    }
  }
  
  registerComponent(id: string, c: IStateComponent)
  {
    let e = this.components.find(c => c.id == id);
    if(e)
    {
      e.comp = c;
    }
    else
    {
      this.components.push({
        id,
        comp: c
      });
    }
  }
  
  storeState(id: string, value: any, store = true)
  {
    let el = this.dict.find(e => e.id == id);
    if(el)
    {
      el.store = store;
      el.value = value;
    }
    else
    {
      this.dict.push({
        id,
        store,
        value
      });
    }
  }
  
  async retrieveState<T>(id: string): Promise<T>
  {
    let s =  this.dict.find(e => e.id == id)?.value;
    if(s == null)
      s = await DbService.get(id);
    return s;
  }
}

export const StateService = new StateServiceClass();