import Worker from 'worker-loader!./../worker/worker';
import { IConfig } from 'm-formatter';
import { AggregateError } from '../interfaces';


export interface IFormatError
{
  message: string;
  stack: string;
  kind: "UNKNOWN" | "PARSER_ERROR" | "FORMATTER_ERROR" | "CSS_ERROR" | "INVALID_CONFIG" | "UNKNOWN_ENTITY";
  innerError: Error;
  meta: any;
}

class FormatServiceClass
{
  worker;
  resolve: (result) => void = null;
  reject: (err) => void = null;
  
  constructor()
  {
    if(typeof(window) !== "undefined")
    {
      this.worker = new Worker();
      this.worker.onmessage = (ev) => {
        let [result, errs] = ev.data;
        
        let errors = JSON.parse(errs) as IFormatError[];
        if(errors.length == 0)
          this.resolve(result);
        else if(errors.length == 1)
          this.reject(errors[0]);
        else
          this.reject(new AggregateError(errors));
      };
    }
  }  
  
  public async format(code: string, config: IConfig): Promise<string>
  {
    return new Promise((resolve, reject) => {
      this.worker.postMessage({
        code,
        config
      });
      this.resolve = resolve;
      this.reject = reject;
    });
  }
  
}

export const FormatService = new FormatServiceClass();