import { IConfig } from 'm-formatter';

export const darkThemeCss = `
.constant.keyword {
  color: #c586c0;
}

.constant {
  color: #d4d4d4;
}

.identifier {
  color: #9cdcfe;
}

.identifier.method-call {
  color: #DCDCAA;
}

.comment {
  color: #6A9955;
}

.operator {
  color: #d4d4d4;
}

.operator.operator-dot {
  color: #D4D4D4;
}

.operator.operator-keyword {
  color: #569CD6;
}


.bracket {
  font-weight: bold;
}

.bracket-0 {
  color: Gold;
}

.bracket-1 {
  color: GoldenRod;
}

.bracket-2 {
  color: DarkGoldenRod;
}

.type {
  color: #4ec9b0;
}

.literal.null {
  color: #569cd6;
}

.literal.string {
  color: #ce9178;
}

.literal {
  color: #dcdcaa;
}

body {
  font-family: monospace;
  background-color: #1e1e1e;
}
`;

export const lightThemeCss = `
.constant {
  color: #3a3a3a;
}

.keyword {
  color: #AF00DB;
}

.constant.unknown-node {
  color: red;
}

.identifier {
  color: #000000;
}

.method-call {
  color: #795E26;
}

.function {
  color: #795E26;
}

.operator, .punctuation {
  color: #000000;
}

.operator.operator-keyword {
  color: #569CD6;
}

.bracket {
  font-weight: bold;
}

.bracket-0 {
  color: Gold;
}

.bracket-1 {
  color: GoldenRod;
}

.bracket-2 {
  color: DarkGoldenRod;
}

.type {
  color: #267F99;
}

.null, .boolean {
  color: #0000FF;
}

.literal {
  color: #569CD6;
}

.string {
  color: #A31515;
}

.comment {
  color: #6A9955;
}

.number {
  color: #098658;
}
`;

export var defaultOptions: IConfig = {
  indentation: "&nbsp;&nbsp;",
  indentationLength: 2,
  lineEnd: "<br/>",
  ws: "&nbsp;",
  lineWidth: 100,
  numBrackets: 3,
  escapeHtmlText: true,
  alignPairedExpressionsByEqual: false,
  inlineCssToggle: true,
  inlineCss: lightThemeCss,
  includeComments: true,
  
};

