import React, { ChangeEvent } from "react";
import {  defaultOptions } from '../options';
import { IConfig } from "m-formatter";
import { FaQuestionCircle } from "react-icons/fa";
import "./FormatterOptions.sass"
import { StateService, IStateComponent } from '../misc/stateService';

interface IState {
  indentLength: string;
  lineWidth: string;
  inlineCss: string;
  alignExpressionsByEqual: boolean;
  numBrackets: string;
  indentLengthErr: string,
  numBracketsErr: string,
  inlineCssErr: string,
  inlineCssToggle: boolean;
  customCss: boolean; 
  includeComments: boolean;
  indentKind: "Spaces"|"Tabs";
  tabWidth: string;
  livePreview: boolean;
  formatOnPaste: boolean;
  copyOnFormat: boolean;
  fontSize: number;
}


export class FormatterOptions extends React.Component<{onChange: () => void}, IState> implements IStateComponent
{
  changeTimeout: any;
  
  constructor(props)
  {
    super(props);
    this.state = this.configToState({...defaultOptions});
    StateService.registerComponent("formatterOptions", this);
  }
  
  getState()
  {
    return this.state;
  }
  
  configToState(options: IConfig): IState
  {
    return {
      indentLength: options.indentationLength.toString(),
      lineWidth: options.lineWidth.toString(),
      inlineCss: options.inlineCss.toString(),
      alignExpressionsByEqual: options.alignPairedExpressionsByEqual,
      numBrackets: options.numBrackets.toString(),
      inlineCssToggle: options.inlineCssToggle,
      includeComments: options.includeComments,
      indentLengthErr: null,
      numBracketsErr: null,
      customCss: false,
      inlineCssErr: null,
      indentKind: "Spaces",
      tabWidth: "2",
      livePreview: true,
      formatOnPaste: false,
      copyOnFormat: true,
      fontSize: 1,
    }
  }
  
  emitChanged = () => {
    if(this.state.livePreview == true && this.props.onChange != null)
    {
      if(this.changeTimeout != null)
      {
        clearTimeout(this.changeTimeout);
        this.changeTimeout = null;
      }
      this.changeTimeout = setTimeout(() => {
        this.props.onChange();
      }, 300);
    }
  }
  
  resetOptions = (ev) => {
    this.setState(this.configToState({...defaultOptions}));
    this.emitChanged();
    ev.preventDefault();
  }

  handleChange = async (ev: ChangeEvent<any>) => {
    let d = {};
    if(ev.target.type == "checkbox")
      d[ev.target.name] = ev.target.checked;
    else
      d[ev.target.name] = ev.target.value;
    this.setState(d);
    this.emitChanged();
  }
  
  async componentDidMount()
  {
    let state = await StateService.retrieveState<IState>("formatterOptions");
    if(state)
      this.setState(state);  
  }
  
  componentWillUnmount()
  {
    StateService.storeState("formatterOptions", this.getState());
  }
  
  getConfig(): IConfig
  {
    this.setState({
      numBracketsErr: null,
      indentLengthErr: null,
      inlineCssErr: null
    });
    function checkValidNumber(num: number)
    {
      return num != null && isNaN(num) == false;
    }
    let success = true;
    let state = {} as any;

    let indentLength = this.state.indentKind == "Spaces" ?  Number.parseInt(this.state.indentLength) : Number.parseInt(this.state.tabWidth);
    let lineWidth = Number.parseInt(this.state.lineWidth);

    let numBrackets = +this.state.numBrackets;
    if(checkValidNumber(numBrackets) == false)
    {
      success = false;
      state.numBracketsErr = "Could not parse integer.";
    }
    else if(numBrackets < 0)
    {
      success = false;
      state.numBracketsErr = "Invalid value, must be >= 0";
    }
    
    
    this.setState(state);
    if(success == false)
      throw new Error("Invalid config");
      
    return  {
      ...defaultOptions,
      indentationLength: indentLength,
      lineWidth: lineWidth,
      numBrackets: numBrackets,
      inlineCss: this.state.inlineCssToggle == true ? this.state.inlineCss : null,
      alignPairedExpressionsByEqual: this.state.alignExpressionsByEqual,
      indentation: defaultOptions.ws.repeat(indentLength),
      includeComments: this.state.includeComments
    };
  }
  
  render()
  {
    return (
      <div className="options-container">
        <div>
          <span className="hint">I will store your preferred options in your browser and remember them when you revisit. <a onClick={this.resetOptions}>Reset to default values.</a></span>
        </div>
        <div className="option" style={{marginTop: "10px"}}>
          <label>
            <span title="Number of characters allowed in one line. The formatter will try to break statements to fit into the line width. Some statements cannot be broken in a good way (like a very long string literal) and therefore the line width can be exceeded in some cases.">Line width<FaQuestionCircle></FaQuestionCircle></span>
            <div style={{display: "flex", alignItems: "center"}}>
              <span style={{marginRight: "10px"}}><b>{this.state.lineWidth}</b></span>
              <input className="slider is-fullwidth" onChange={this.handleChange} type="range" step="1" min="0" max="300" value={this.state.lineWidth} name="lineWidth" ></input>
            </div>
          </label>
        </div>
        <div className="option">
          <div style={{display: "flex", alignItems: "center"}}>
            <label>
              <span title="Use Tab or Spaces to indent">Indentation<FaQuestionCircle></FaQuestionCircle></span>
            </label>
            <div className="select is-small" style={{marginLeft: "10px"}}>
              <select onChange={this.handleChange} value={this.state.indentKind} name="indentKind">
                <option value="Spaces">Spaces</option>
                <option value="Tabs">Tabs</option>
              </select>
            </div>
          </div>
        </div>
        {
          this.state.indentKind == "Spaces" ? (
            <div className="option">
              <label>
                <div>
                    <span title="Number of spaces per indentation">Spaces per Indentation<FaQuestionCircle></FaQuestionCircle></span>
                </div>
                <div style={{display: "flex", alignItems: "center"}}>
                  <span style={{marginRight: "10px"}}><b>{this.state.indentLength}</b></span>
                  <input className="slider is-fullwidth" onChange={this.handleChange} type="range"  step="1" min="1" max="20" value={this.state.indentLength} name="indentLength" ></input>
                </div>
              </label>
            </div>
          ): (
            <div className="option">
            <label>
              <div>
                  <span title="Number of whitespaces per tab (only used for display in the codeblock)">Tab width<FaQuestionCircle></FaQuestionCircle></span>
              </div>
              <div style={{display: "flex", alignItems: "center"}}>
                <span style={{marginRight: "10px"}}><b>{this.state.tabWidth}</b></span>
                <input className="slider is-fullwidth" onChange={this.handleChange} type="range"  step="1" min="1" max="20" value={this.state.tabWidth} name="tabWidth" ></input>
              </div>
            </label>
          </div>
          )
        }
        <div className="option">
          <label>
            <span title="Number of different bracket classes. Brackets are given classes bracket-{number} for you to style matching brackets specifically. All kinds of brackets '{', '(' and '[' are treated equally.">Number of brackets<FaQuestionCircle></FaQuestionCircle></span>
            <div style={{display: "flex", alignItems: "center"}}>
                <span style={{marginRight: "10px"}}><b>{this.state.numBrackets}</b></span>
                <input className="slider is-fullwidth" onChange={this.handleChange} type="range"  step="1" min="1" max="10" value={this.state.numBrackets} name="numBrackets" ></input>
            </div>
          </label>
        </div>
        <div className="option">
          <label>
            <input className="checkbox" name="includeComments" onChange={this.handleChange} checked={this.state.includeComments} type="checkbox"></input>
            <span title="Include comments in the format result"> Include Comments<FaQuestionCircle></FaQuestionCircle></span>
          </label>
        </div>
        <div className="option">
          <label>
            <input className="checkbox" name="alignExpressionsByEqual" onChange={this.handleChange} checked={this.state.alignExpressionsByEqual} type="checkbox"></input>
            <span title="Align paired expressions in the same scope by their equal sign. Paired expressions are for example 'k = 10'. If you have more than one paired expression in one scope this setting will fill the left identifier with as many spaces as needed to align the equal signs on each line."> Align paired expressions by equal<FaQuestionCircle></FaQuestionCircle></span>
          </label>
        </div>
        <div className="option">
          <label>
            <input className="checkbox" name="formatOnPaste" onChange={this.handleChange} checked={this.state.formatOnPaste} type="checkbox"></input>
            <span title="Automatically trigger formatting when pasting code into the window. Please note that pasting to the code text area will not trigger foratting as it will result in confusing behavior.">Format on paste to window<FaQuestionCircle></FaQuestionCircle></span>
          </label>
        </div>
        <div className="option">
          <label>
            <input className="checkbox" name="inlineCssToggle" onChange={this.handleChange} checked={this.state.inlineCssToggle} type="checkbox"></input>
            <span title="Inline the applied css attributes as style attributes on the html elements. If disabled, no inlining will be performed and only the class tags will be applied to the html elements.">InlineCss<FaQuestionCircle></FaQuestionCircle></span>
          </label>
        </div>
        <div className="option">
          <label>
            <input className="checkbox" name="copyOnFormat" onChange={this.handleChange} checked={this.state.copyOnFormat} type="checkbox"></input>
            <span title="Automatically paste formatted code to clipboard after every formatting.">Auto copy-to-clipboard<FaQuestionCircle></FaQuestionCircle></span>
          </label>
        </div>
        <div className="option">
          <label>
            <input className="checkbox" name="livePreview" onChange={this.handleChange} checked={this.state.livePreview} type="checkbox"></input>
            <span title="Automatically trigger formatter on settings change">Live Preview<FaQuestionCircle></FaQuestionCircle></span>
          </label>
        </div>
        <div className={`option ${this.state.inlineCssToggle == false ? "no-display" : ""}`}>
          <label>
          <input className="checkbox" name="customCss" onChange={this.handleChange} checked={this.state.customCss} type="checkbox"></input>
          <span title="Use custom css">Use custom css<FaQuestionCircle></FaQuestionCircle></span>
          <textarea cols={100} rows={25} name="inlineCss" title="Check 'Use custom css' to edit" disabled={this.state.customCss == false} onChange={this.handleChange} className={`textarea ${this.state.inlineCssToggle == false ? "no-display" : ""}`} value={this.state.inlineCss} placeholder="inline css"></textarea>
          </label>
        </div>
        <div className="option" style={{marginTop: "10px"}}>
          <label>
            <span title="Editor Fontsize">Editor Fontsize<FaQuestionCircle></FaQuestionCircle></span>
            <div style={{display: "flex", alignItems: "center"}}>
              <span style={{marginRight: "10px"}}><b>{this.state.fontSize}</b></span>
              <input className="slider is-fullwidth" onChange={this.handleChange} type="range" step=".05" min="0" max="2" value={this.state.fontSize} name="fontSize" ></input>
            </div>
          </label>
        </div>
      </div>
    )

  }
}